import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/home/home.vue'
import city from '../components/city/city.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/:id',
      name: 'city',
      props: true,
      component: city
    }
  ]
})



