<template>
  <div class="city-card">
        <b-card no-body class="card">
        <b-card-header>
          {{data.name}}
        </b-card-header>
        <b-card-img class="loc-img" :src="getImgUrl(data.latitude, data.longitude)"/>
        <b-list-group flush>
          <b-list-group-item class="last-obs-temp">
            <p class="time_note">Lastest obdservation</p>
            <p class="last-obs-time">{{data.lastObservation.temperature}}&#8451;</p>
          </b-list-group-item>
          <b-list-group-item>
            <router-link :to="{ name: 'city', params: { id: data.id, city:data}}">Details</router-link>
          </b-list-group-item>
        </b-list-group>
        <b-card-footer>
          <p class="time_note">Lastest obdservation</p>
          <p class="last-obs-time">{{date(data.lastObservation.time)}}</p>
        </b-card-footer>
    </b-card>
  </div>
</template>

<script>
export default {
  name: 'city-card',
  props: ['data'],
  methods: {
    getImgUrl (lat, lon) {
      return 'https://maps.googleapis.com/maps/api/staticmap?' +
        'center=' + lat + ',' + lon +
        '&zoom=10' +
        '&size=350x350' + /* origina image size */
        '&style=element:labels|visibility:off' + /* remove all labels */
        '&style=feature:all|visibility:off' + /*  remove all features */
        '&style=feature:landscape|visibility:simplified|color:0x454545' + /* show only landscape */
        '&style=feature:water|visibility:simplified|color:0x919191' + /* show only water */
        '&key=AIzaSyD03gW0mfYL5HwkE-avIlTDMV0wZ2-xHM4'
    },
    date (UTCepoch) {
      var date = new Date(0)
      date.setUTCSeconds(UTCepoch)
      return date.toUTCString()
    }
  }
}
</script>

<style>
.loc-img {
  object-fit: none;
  height: 100%;
  width: 100%;
  margin: auto;
}
.card{
  max-width: 300px;
}
.card-header{
  font-size: 30px;
}
.time_note{
  font-size: 12px;
}
</style>



