<script>
import { Line, mixins } from 'vue-chartjs'
export default {
  extends: Line,
  mixins: [mixins.reactiveProp],
  props: ['width', 'height', 'chartData'],
  options: {
    maintainAspectRatio: false,
    responsive: true,
    showLines: false,
    elements: {
      line: {
        tension: 0
      }
    }
  },
  mounted () {
    this.renderChart(this.chartData, this.options)
  }
}
</script>



