<template>
  <div class="home">
    <b-card-group columns>
      <cityCard :key="index" :data="city" v-for="(city, index) in cities">{{city}}</cityCard>
    </b-card-group>
  </div>
</template>

<script>
import cityCard from './citycard.vue'

export default {
  name: 'home',
  data () {
    return {
      cities: {},
      pages: {}
    }
  },
  methods: {
    getLocationsData () {
      this.axios
        .get('/locations')
        .then(response => {
          this.cities = response.data._embedded.locations
          this.pages = response.data.page
        })
        .catch(error => {
          console.log('error')
          console.log(error)
        })
    }
  },
  mounted () {
    this.getLocationsData()
  },
  components: {
    cityCard
  }
}
</script>

<style scoped>
.city_row{
  text-decoration: none;
}

.city_container{
  margin: 0 0 50px 0;
  padding: 0;
}

.city_name, .last_temp{
  text-align: left;
  font-size: 55px;
  margin-left: 0;
}

.last_time{
  text-align: left;
  font-size: 15px;
  margin-left: 0;
}

.low_icon,
.high_icon{
  text-align: right;
  margin-right: 0 ;
}

.low_time,
.high_time{
  font-size: 15px;
}

.low_temp,
.high_temp{
  font-size: 25px;
  text-align: right;
  margin-left: 0;
}
</style>



