<template>
  <div class='city'>
    <h3>{{city.name}}</h3>
    <chart
      :width="40"
      :height="20"
      :chartData='{ labels: chartLabes, datasets: chartDatasets}'>
    </chart>
    <form>
      <input type='number'>
      <button type='button' >Save</button>
    </form>
    </div>
</template>

<script>
import chart from '../city/tempchart.vue'
export default {
  name: 'city',
  data () {
    return {
      city: {},
      pages: {},
      chartLabes: [],
      chartDatasets: [{
        label: 'Temperature',
        data: []
      }]
    }
  },
  methods: {
    getCityObservations () {
      this.axios
        .get('/locations' + this.$route.path + '/observations')
        .then(response => {
          this.pages = response.data.page
          response.data._embedded.observations.reverse().forEach(obs => {
            this.chartDatasets[0].data.push(obs.temperature)
            let date = new Date(0)
            date.setUTCSeconds(obs.time)
            this.chartLabes.push(date.getHours() + ':' + date.getMinutes())
          })
        })
        .catch(error => {
          console.log('error')
          console.log(error)
        })
    },
    postNewObservation () {
      this.axios
        .post('/observations', {
          location: this.$route.path,
          temperature: -2,
          time: 0
        })
        .then(response => {

        })
        .catch(error => {
          console.log('error')
          console.log(error)
        })
    },
    getLocationData () {
      this.axios
        .get('/locations' + this.$route.path)
        .then(response => {
          this.city = response.data
        })
        .catch(error => {
          console.log('error')
          console.log(error)
        })
    }
  },
  created () {
    // if page is loaded out side of the app router, city data is null.
    if (!this.$route.params.city) {
      this.getLocationData()
    } else {
      this.city = this.$route.params.city
    }
    this.getCityObservations()
  },
  components: {
    chart
  }
}
</script>

<style scoped>

</style>



